.Loginpage{
    /* background-image: url(F:\working\ecommerce\public\shopping-cart-supermarket-generative-ai.jpg); */
    /* border: 3px solid #FEE715; */
    /* background-color: rgba(214, 59, 59, 0.2);\ */
    backdrop-filter: blur(8px);
    border-top: 2px groove #FF6960;

    padding-top: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 87.4vh;
    width: 100%;
    background-position:fill;
    background-size:cover;
    min-height: 0%;
}

.login_box{
    position: relative;
    width: 450px;
    backdrop-filter: blur(50px);
    border: 2px solid #C82100;
    border-radius: 15px;
    padding: 5em 2.5em 3em 2.5em;
    box-shadow: 0px 0px 10px 2px #C82100;
}

.login_head{
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%);
    display: flex;
    text-shadow: #2A2B31;
    align-items: center;
    justify-content: center;
    height: 80px;
}

.login_head span{
    font-size: 30px;
    font-weight: 800;
    color: #2A2B31;
    text-shadow: 2px 2px 6px #bfd4fa;
}

.input_box{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 20px 0; 
}

.input-field{
    width: 100%;
    height: 55px;
    font-size: 16px;
    background: transparent;
    color: #2A2B31;
    border: 2px solid #2A2B31;
    padding-inline: 20px 50px;
    border-radius: 30px;
    outline: none;
}

#user{
    margin-bottom: 10px;
}

.label{
    position: absolute;
    top:18px;
    left: 240px;
    transition: 0.2px;
    color: #2A2B31;
}

.input-field:focus ~ .label,
.input-field:valid .label 
{
  position: absolute;
  top: -10px;
  left: 20px;
  font-size: 16px;
  transition: 0.3s;
  background-color: #2A2B31;
  border-radius: 30px;
  color: #f9574f;
  padding: 0 10px;
} 

.input_box i{
    position: absolute;
    top: 18px;
    right: 25px;
    font-size: 20px;
    color: #f9574f;
}

.input-submit {
    width: 60%;
    height: 50px;
    margin-top: 15px;
    background: #2A2B31;
    color: #f9574f;
    font-size: 20px;
    font-weight: 700;
    border: none;
    border-radius: 30px;
    cursor: pointer;
    transition: 0.3s;
  }

  .input-submit:hover {
    color:#2A2B31 ;
    background-color: #f9574f;
    box-shadow: 0 0 20px #C82100;
    transform: scale(1);
  }
  
  .register {
    text-align: center;
  }

  .remember-forgot {
    display: flex;
    justify-content: space-between;
    font-size: 15px;
    color: #2A2B31;
  }

  .forgot a{
    text-decoration: none;
    color: #f9574f;
    cursor: pointer;
  }

  .forgot a:hover {
    text-decoration: underline;
    color: #2A2B31;
  }

  .register{
    color: #2A2B31;
  }

  .reg{
    color: #C82100;
  }

  .inlo{
    color: #EB8300;
    text-decoration: none;
  }

  .inlo:hover{
    color: #2A2B31;
    text-decoration: none;
  }