.About{
    /* background-color: rgba(249, 104, 104, 0.2); */
    backdrop-filter: blur(8px);
    border-top: 2px groove #FF6960;
    max-width: 100%;
    height: 90vh;
    color: #C82100;
    font-size: 20px;
    font-weight: 700;
    text-align: center;
    
}

.about-title{
    color: #C82100;
}

.about-con{
    justify-content: center;
    color: #2A2B31;
    align-items: center;
    display: flex;
    margin: auto;
    padding: 3% 5%;
    flex-direction: column;
}
.about-content{
    max-width: 900px;
    line-height: 1.5;
    letter-spacing: 1px;
    margin: 30px;
    /* margin-bottom: 80px; */
    }
    @media screen and (max-width: 800px) {
        .about-content{
            width: 90%;
            font-size: 16px;
            }
}

@media screen and (max-width: 600px){
    .about-con {
        padding: 10% 5%;
    }
}


