@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap');

*{
  font-family: 'Roboto', sans-serif;
  padding:0;
  /* margin: 10px; */
  box-sizing: border-box;
}

.App{
  background-image: url(F:\working\ecommerce\public\3d-image-copy.jpg);
  height: 100vh;
  /* filter: blur(8px); */
  background-position:bottom;
  background-size:cover;
}
.body{
  height: 100%;
  background-color: #2A2B31;
  border: 3px solid black;
}

.nav{
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 75px;
  color:#C82100;
  background-color:transparent;
  /* border-bottom: 02px  ; */
  margin-bottom: -10px;
}

.os{
  font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  left: 50px;
  font-weight: 900;
  font-size: 33px;
  /* text-shadow: 0px 2px 2px rgb(255, 131, 131),0px 5px 5px rgb(255, 131, 131),0px 8px 8px rgb(255, 131, 131);
  background: transparent;
  color: #C82100;
  background-clip: text; */
  text-shadow: 3px 2px 3px rgb(255, 131, 131);
}

.navlink{
  display: flex;
  justify-content: flex-end;
  list-style-type: none;
  width: 75%;
}


.home{
  text-decoration: none;
  justify-content: space-between;
  padding: 10x 20px;
  margin: 10px 20px;
}

.home {
  font-size: 18px;
  color: #FF6960;
  font-family: inherit;
  font-weight: 800;
  cursor: pointer;
  position: relative;
  border: none;
  background: none;
  text-transform: uppercase;
  transition-timing-function: cubic-bezier(0.25, 0.8, 0.25, 1);
  transition-duration: 400ms;
  transition-property: color;
}

.home:focus,
.home:hover {
  color: #EB8300;
}

.home:focus:after,
.home:hover:after {
  width: 100%;
  left: 0%;
}

.home:after {
  content: "";
  pointer-events: none;
  bottom: -2px;
  left: 50%;
  position: absolute;
  width: 0%;
  height: 2px;
  background-color: #C82100;
  transition-timing-function: cubic-bezier(0.25, 0.8, 0.25, 1);
  transition-duration: 400ms;
  transition-property: width, left;
}

.menuicon{
    display:none;
}


@media screen and (max-width:600px) {
    .os{
      text-align: start;
      display: flex;
      position: absolute;
      /* top: 10px; */
      left: 35px;
    }

    .navlink{
      display: none;
      flex-direction: column;
    }

    .navmob{
      position:absolute;
      display: flex;
      flex-direction: column;
      list-style-type: none;
      top:70px;
      padding:10px;
      margin-top: -3px;
      text-align: center;
      width: 100%;
      height:max-content;
      background-color: rgba(255, 104, 96, 0.687);
      transition: all 1s ease-in-out;
      /* background: transparent; */
      backdrop-filter: blur(20px);
      z-index: 1;
    }

    .navmob li{
      margin: 12px 0;
    }


    .home{
      color:#DADED4;
      text-align: center;
      padding: 32px;
      width: 100%;
      /* transition: all 1s ease-in-out; */
    }


    .menuicon{
      display:block;
      position: absolute;
      font-size: 28px;
      color:#FF6960;
      background-color: transparent;
      border: none;
      top: 15px;
      right: 25px;
    }

     .home { 
      border-radius: 80px;
      transition: all 0.3s;
      cursor: pointer;
      color: #2A2B31;
      font-weight:bolder;
      background: transparent;
      font-size: 14px;
      padding:10px;
      font-family: 'Montserrat', sans-serif;
      z-index: 1;
    }

    .home:hover{
      background: transparent;
      color: #C82100;
      font-size: 16px;
      font-weight: 700;
    }
   }