.details{
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(112, 109, 109, 0.116);
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.de_detail{
    /* background: hsla(48, 97%, 51%, 1);
    background: radial-gradient(circle, hsla(48, 97%, 51%, 1) 42%, hsla(50, 100%, 49%, 1) 63%, hsla(31, 100%, 59%, 1) 100%, hsla(231, 8%, 18%, 1) 100%);
    background: -moz-radial-gradient(circle, hsla(48, 97%, 51%, 1) 42%, hsla(50, 100%, 49%, 1) 63%, hsla(31, 100%, 59%, 1) 100%, hsla(231, 8%, 18%, 1) 100%);
    background: -webkit-radial-gradient(circle, hsla(48, 97%, 51%, 1) 42%, hsla(50, 100%, 49%, 1) 63%, hsla(31, 100%, 59%, 1) 100%, hsla(231, 8%, 18%, 1) 100%);
    filter: progid: DXImageTransform.Microsoft.gradient( startColorstr="#FBCC08", endColorstr="#FCD400", GradientType=1 );   */
    background: transparent;
    backdrop-filter: blur(70px);
    max-width: 600px;
    width: 100%;
    border: 2px solid #2A2B31;
    border-radius: 2rem;
    box-shadow: 8px 14px 12px #f9574f;
    min-height: 300px;
    display: flex;
    flex-direction: column;
    position: relative;
}

.de_title{
    padding: 1rem;
    /* border-bottom: 1px solid black; */
    margin-top: -30px;
    font-size: 21px;
    color: #C82100;
    border-bottom: 1.5px solid #2A2B31;
    text-shadow: 2px 2px 5px grey;
}

.de_content{
    flex: 1;
}

.de_content ul{
    list-style-type:none;
    color: #2A2B31  ;
    text-align: center;
    margin-left: 40px;
    margin-right: 40px;
}

.de_content  li{
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #2A2B31;
  line-height: 1.5em;
  font-size: 18px;
  letter-spacing: 1px;
  padding:20px;
  width: auto;
  /* max-width: 100px; */
  /* font-weight: 650; */
}

.de_footer{
    padding: 0.5rem;
    border-top: 1.5px solid #2A2B31;
    display: flex;
    justify-content:space-around;
    margin-bottom: -30px;
}

.de_close{
    position: absolute;
    color: #2A2B31;
    font-size: 2rem;
    font-weight: bold;
    top:0.2rem;
    right: 1rem;
    cursor: pointer;
    margin-top: 10px;
}

.btn {
    justify-content: space-around;
    margin: 7px 7px 0 7px ;
    padding: 10px 15px;
    border: 0;
    border-radius: 100px;
    background-color: #2A2B31;
    color: #f9574f;
    font-weight: Bold;
    transition: all 0.5s;
    -webkit-transition: all 0.5s;
  }
  
  .btn:hover {
    color: #C82100 ;
    background-color: #f9574f;
    box-shadow: 0 0 20px #2A2B31;
    transform: scale(1.1);
  }
  
  .btn:active {
    background-color: #2A2B31;
    transition: all 0.25s;
    -webkit-transition: all 0.25s;
    box-shadow: none;
    transform: scale(0.98);
  }

  .prodimg{
    filter: drop-shadow(12px 12px 12px #2A2B31);
    margin-left: 10px;
    margin-top: 10px;
    margin-right: 40px;
  }

  .star{
    font-size:17px;
    padding-top: 4px;
  }
  .offer{
    margin-top:1px;
    margin-bottom: -20px;
    align-items: center;
    text-decoration: line-through;
    font-size: 14px;
    color: #2A2B31;
    font-weight:900;
  }

  .offer1{
    color: #C82100;
  }
  

  .price{
    font-size: 17px;
    margin-bottom: -px;
    margin-top: 20px;
    padding-top: 5px;
    font-weight: 700;
    color: #2A2B31;
  }

  .a1{
    color: #C82100;
    display: flex;
    justify-content: center;
    font-weight: 700;
    font-size: 17px ;
    margin-bottom: -20px;
    margin-top: -10px;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  }

  .de{
    display: flex;
    /* padding-top: 20px; */
    margin-top: -10px;
    margin-bottom: -20px;
    justify-content: space-around;
    align-items: center;
    color: #C82100;
  }

