@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap');

.search{
    text-align: center;
    margin: 20px;
    padding: 20px;
}


::placeholder{
    color: #2A2B31;
    font-size: 18px;
    text-align: center;
    align-items: center;
    justify-content: center;
    font-weight: 450;
}

.App1{
  display: flex;
}
.count2{
  display: flex;
}
.count3{
  border: none;
  background-color: transparent;
  color: #2A2B31;
  margin-left: 10px;
  font-size: 28px;
  position: absolute;
  top:5px;
  left: 60px;
  font-weight:800;
  max-width: 40px;
}


.home1::-webkit-scrollbar {
  display: none;
  filter: blur(8px);
}

.home1{
  /* background-image: url(F:\working\ecommerce\public\shopping-cart-supermarket-generative-ai.jpg); */
  padding-top: 20px;
  overflow:auto;
  backdrop-filter: blur(8px);
  border-top: 2px groove #FF6960;
  height:90vh;
  /* margin-top:-10px; */
  /* background-color: rgba(214, 59, 59, 0.25); */
  width: 100%;
  background-position:fill;
  background-size:cover;
  min-height: 0%;
}

.input {
    font-family: -apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;
    font-weight: 450;
    font-size: 18px;
    color:#474344;
    background-color:#FF6960;
    border-radius: 1.2vw 0  0 1.2vw;
    border: 0px solid #EB8300;
    outline: none;
    padding: 0.4vw;
    transition: .4s;
  }
  

  .input1 {
    margin-left: -1px;
    font-family: -apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;
    font-weight: 500;
    font-size: 18px;
    color: #474344;
    background-color:#FF6960;
    border-radius: 0 1.2vw 1.2vw 0;
    border: none;
    outline: none;
    padding: 0.4vw;
    transition: .4s;
  }
  

  .ul{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px,1fr));
    list-style-type: none;
    margin: 10px;
    padding: 10px;
  }

  .ul li{
    justify-content: space-around;
     border: 2px solid #2A2B31 ;
    /* background-color: #f4b393; */
    background: transparent;
    backdrop-filter: blur(40px);
    background-color: rgba(230, 154, 154, 0.379);
    border-radius: 50px; 
    padding: 10px;
    font-weight:550;
    color: #2A2B31;
    margin: 0 15px 50px 20px;
    text-align:center;
  }

  .ul li:hover{
    background: transparent;
    backdrop-filter: blur(70px);
    border: 2px solid #FF6960 ;
    /* transform: rotateX(60deg); */
    transform: scale(1.07) translateY(-6px);
    background: linear-gradient(rgba(0,0,0,0),rgb(252, 120, 120));
    box-shadow: 8px 8px 8px #C82100;
    transition:0.4s;
    cursor: pointer;
  }

  .cart {
    margin: 20px 20px 0 20px ;
    padding: 10px 20px;
    border: 0;
    border-radius: 100px;
    background-color: #2A2B31;
    color: #FF6960;
    font-weight: Bold;
    transition: all 0.5s;
    -webkit-transition: all 0.5s;
  }
  
  .cart:hover {
    color:#2A2B31 ;
    background-color: #FF6960;
    box-shadow: 0 0 20px #2A2B31;
    transform: scale(1.1);
  }
  
  .cart:active {
    background-color: #2A2B31;
    transition: all 0.25s;
    -webkit-transition: all 0.25s;
    box-shadow: none;
    transform: scale(0.98);
  }

  .image{
    padding: 10px;
    width: 100px;
    height: 100px;
    filter: drop-shadow(4px 4px 8px #2A2B31);
  }  

  .footer{
    text-align: center;
    justify-content: center;
    color: #FCD400;
    background-color: #2A2B31;
    font-family: 'Poppins', sans-serif;
    font-size: medium;
    font-weight: 300;
    padding: 20px;
    margin-top:-10px;
  }

  .button {
    font-size: 14px;
    padding: 0.2em 0.4em;
    border: transparent;
    box-shadow: 2px 2px 4px rgba(0,0,0,0.4);
    background: #FF6960;
    color: #2A2B31;
    border-radius: 4px;
    margin-top:10px;
    margin-bottom: -5px;
   }

   .price1{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: -15px;
    margin-top: -10px;
   }

   .off{
    text-decoration: line-through;
    padding: 5px;
    font-size: 14px;
   }

   .ul li:hover .image{
    transform: scale(1.2);
    filter: drop-shadow(4px 4px 8px #C82100)
   }
  

   .btnn {
    padding: 10px 10px;
    background: none;
    border: 2px solid #2A2B31;
    font-size: 28px;
    color: #C82100;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    transition: all 0.3s;
    border-radius: 100%;
    background-color: #FF6960;
    font-weight: bolder;
    /* box-shadow: 0 2px 0 2px #2A2B31; */
    margin-left: 20px;
    display: flex;
    align-items: center;
    margin-bottom: -10px;
   }
   
   .btnn:before {
    content: "";
    position: absolute;
    width: 100px;
    height: 120%;
    background-color: #474344;
    top: 50%;
    transform: skewX(30deg) translate(-150%, -50%);
    transition: all 0.5s;
   }
   
   .btnn:hover {
    background-color: #2A2B31;
    color: #FF6960;
    box-shadow: 0 2px 0 2px #FF6960;
   }
   
   .btnn:hover::before {
    transform: skewX(30deg) translate(150%, -50%);
    transition-delay: 0.1s;
   }
   
   .btnn:active {
    transform: scale(0.9);
   }

   .emptycart{
    text-align: center;
    font-size: 22px;
    color: #FEE715;
    font-weight: 700;  
  }