.addcart{
    height: 90vh;
    width: 40%;
    border: 3px solid #2A2B31;
    border-radius: 10px;
    background-color: rgba(230, 154, 154, 0.379);
    background-color: transparent;
    backdrop-filter: blur(70px);
    overflow: auto;
}

.addcart::-webkit-scrollbar {
    display: none;
}

.cart1{
    border-bottom: 3px solid #2A2B31;
    background-color: transparent;
    backdrop-filter: blur(20px);
    color: #2A2B31;
    display: flex;
    padding: 5px;
}

.carticon{
    margin-left: 200px;
    margin-top: 20px;
}

.count1{
    border: none;
    background-color: transparent;
    backdrop-filter: blur(20px);
    color: #2A2B31;
    margin-left: 7px;
    font-size: 25px;
    font-weight:800;
    max-width: 40px;
}

.count{
    margin-top: 14px;
}

.prods ul {
    list-style-type: none;
    text-align: center;
    margin-top: 4px;
    /* background: hsla(48, 97%, 51%, 1);
    background: radial-gradient(circle, hsla(48, 97%, 51%, 1) 42%, hsla(50, 100%, 49%, 1) 63%, hsla(31, 100%, 59%, 1) 100%, hsla(231, 8%, 18%, 1) 100%);
    background: -moz-radial-gradient(circle, hsla(48, 97%, 51%, 1) 42%, hsla(50, 100%, 49%, 1) 63%, hsla(31, 100%, 59%, 1) 100%, hsla(231, 8%, 18%, 1) 100%);
    background: -webkit-radial-gradient(circle, hsla(48, 97%, 51%, 1) 42%, hsla(50, 100%, 49%, 1) 63%, hsla(31, 100%, 59%, 1) 100%, hsla(231, 8%, 18%, 1) 100%);
    filter: progid: DXImageTransform.Microsoft.gradient( startColorstr="#FBCC08", endColorstr="#FCD400", GradientType=1 );   */
    color: #2A2B31;
    background-color: transparent;
}

.prods ul li{
    border: 3px solid #2A2B31;
    border-radius: 18px;
    display: flex;
    padding:  10px;
    background-color: #FF6960;
    margin-left: 5px;
    margin-right: 5px;
    /* margin-top:-20px; */
    align-items: center;
    justify-content: space-between;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 600;
}

.prods ul button {
    /* font-size: 13px; */
    cursor: pointer;
    outline: none;
    border: none;
    background-color: #C82100;
    color: #C82100;
}

.prods ul button {
    /* margin: 10px 10px 0 10px ; */
    padding: 8px 8px;
    border: 0;
    border-radius: 100px;
    background-color: #2A2B31;
    color: #FF6960;
    font-weight: Bold;
    transition: all 0.5s;
    -webkit-transition: all 0.5s;
  }
  
  .prods ul button:hover {
    color:#2A2B31 ;
    background-color: #FF6960;
    box-shadow: 0 0 20px #2A2B31;
    transform: scale(1.1);
  }
  
  .prods ul button:active {
    background-color: #FF6960;
    transition: all 0.25s;
    -webkit-transition: all 0.25s;
    box-shadow: none;
    transform: scale(0.98);
  }

  .total{
    position: sticky;
    position: -webkit-sticky;
    bottom: 0px;
    padding-bottom: 10px;
    margin-top:77vh;
    background-color:#FF6960;
    font-size: 15px;
    font-weight: 600;
    text-align: right;
    color: #2A2B31;
    border-top: 3px solid #2A2B31;
  }

  .total h2 {
    margin-right: 10px;
  }

  .img1{
    border: 2px solid #2A2B31;
    border-radius: 40px;
    margin: 5px;
    background-color: #2A2B31;
    padding: 5px;
  }

  .closeview{
    background-color: #2A2B31;
    width: 0px;
    height: 0px;
    display: flex;
    color: #2A2B31;
    align-items: center;
    justify-content: center;
    font-size: 2.5rem;
    font-weight: bold;
    cursor: pointer;
    margin-top: 10px;
    margin-left: 29px;
  }

  .emptycart{
    color: #2A2B31;
  }

  @media screen and (max-width:600px) {
    .addcart{
      /* display: flex; */
      height: 88.2vh;
      width: 400%;
      border: 3px solid #2A2B31;
      border-radius: 10px;
      background-color: rgba(230, 154, 154, 0.379);
      background-color: transparent;
      backdrop-filter: blur(70px);
      overflow: auto;
  }

  .carticon{
    margin-left: 110px;
    margin-top: 20px;
}

.count1{
    border: none;
    background-color: transparent;
    backdrop-filter: blur(20px);
    color: #2A2B31;
    margin-left: 7px;
    font-size: 25px;
    font-weight:800;
    max-width: 40px;
}

.count{
    margin-top: 14px;
    margin-right: 5px;
}

  }