    justify-content: start;
    /* align-items: center; */
    flex-direction: column;
    text-align: start;
    background:linear-gradient(to left,rgba(0,0,0,0),rgba(255, 102, 102, 0.7) );
    border-radius: 20px;
    padding: 20px;
  }

}